<template>
    <div class="align-items-center justify-content-center py-5 mt-5" >
        <div class="box text-center">
            <p id="title_transaction_id">Pembelian pian dicatat dengan no. pesanan </p>
            <span id="transaction_id">{{loading ? '-' :transaction.transaction_id}}</span>
        </div>
        <div class="box information">
            <span class="d-block text-center" id="total_price">Rp{{loading ? '-' :toCurrency(transaction.total_price)}}</span>
            <span class="d-block text-justify">{{payment.aggregate.description}}</span>
            <span class="d-block text-center" id="cta">{{payment.aggregate.cta}}</span>
        </div>
    </div>
</template>
<script>
export default {
    name:'Checkout',
    data(){
        return {
            loading:false,
            transaction:{
                total_price:null,
                transaction_id:null
            },
            payment:{
                image:null,
                account_number:null,
                aggregate: {
                    method:null,
                    description: null,
                    cta: null
                }
            }
        }
    },
    created(){
        this.loading = true
        this.axios.get(this.$store.state.url_api + "payments/purchases/"+this.$route.params.id,{
            headers:{
                "Authorization" : "Bearer "+localStorage.token
            }
        }).then(response => {
            if(response.data.meta.status=="OK"){
                this.transaction = Object.assign({},this.transaction,response.data.data.transaction);
                this.payment = Object.assign({},this.payment,response.data.data.payment);
                this.loading = false
            }
        })
    },
    methods:{
        toCurrency(angka) {
            angka += '';
			// angka = angka.replace(".",",");
			var x = angka.split('.');
			var x1 = x[0];
			var x2 = x.length > 1 ? '.' + x[1] : '';
			var rgx = /(\d+)(\d{3})/;
			while (rgx.test(x1)) {
				x1 = x1.replace(rgx, '$1' + '.' + '$2');
			}
			return x1 + x2;
        },
    }
    
}
</script>

<style scoped>
    .box{
        background-color:  rgba(245, 245, 245, 0.973);
        width:100%;
        padding:20px 10px;
    }

    .box #transaction_id{
        font-size:1.1em;
        color:rgb(243, 152, 17);
        font-weight: 500;
        letter-spacing: .8px;
    }

    .box.information{
        margin-top:20px;
        /* background-color: rgba(245, 245, 245, 0.973); */
    }

    #total_price{
        font-size:3em;
        margin-bottom:20px;
    }

    #cta{
        font-size: 1.6em;
        margin-top:15px;
        font-weight: 400;
        letter-spacing: 1px;
    }
</style>